/* login.css */

.form-container {
  max-width: 400px;
  margin: 4rem auto;
  padding: 2rem 2.5rem;
  background-color: #ffffff;
  border-radius: 12px;
  box-shadow: 0 8px 20px rgba(0, 0, 0, 0.1);
  font-family: Arial, sans-serif;
  text-align: center;
}

.form-box h2 {
  color: #2ecc71;
  margin-bottom: 1.5rem;
  font-weight: 700;
  font-size: 2rem;
}

.form-box form {
  display: flex;
  flex-direction: column;
  gap: 1.2rem;
}

.form-box input {
  padding: 0.75rem 1rem;
  font-size: 1rem;
  border: 2px solid #2ecc71;
  border-radius: 8px;
  outline: none;
  transition: border-color 0.3s ease;
}

.form-box input:focus {
  border-color: #27ae60;
}

.btn-green {
  padding: 0.8rem 1rem;
  background-color: #2ecc71;
  color: white;
  font-size: 1.1rem;
  font-weight: 700;
  border: none;
  border-radius: 25px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.btn-green:hover {
  background-color: #27ae60;
}

.message {
  color: #e74c3c;
  margin-top: 1rem;
  font-weight: 600;
}
