/* login.css */
.form-container {
  display: flex;
  justify-content: center;
  margin-top: 4rem;
}

.form-box {
  background-color: white;
  padding: 2rem 3rem;
  border-radius: 10px;
  box-shadow: 0 0 10px rgb(0 0 0 / 0.1);
  width: 320px;
  text-align: center;
}

.form-box h2 {
  margin-bottom: 1.5rem;
  color: #2ecc71;
}

.form-box input {
  display: block;
  width: 100%;
  padding: 0.6rem 1rem;
  margin-bottom: 1.2rem;
  border: 1px solid #ccc;
  border-radius: 6px;
  font-size: 1rem;
}

.btn-green {
  background-color: #2ecc71;
  color: white;
  border: none;
  padding: 0.6rem 1rem;
  font-weight: bold;
  border-radius: 20px;
  cursor: pointer;
  transition: background-color 0.3s ease;
  width: 100%;
  font-size: 1.1rem;
}

.btn-green:hover {
  background-color: #27ae60;
}

.message {
  margin-top: 1rem;
  color: red;
  font-weight: bold;
}
