.home-container {
  max-width: 900px;
  margin: 2rem auto;
  padding: 0 1rem;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  color: #333;
}

.home-header {
  text-align: center;
  margin-bottom: 3rem;
}

.home-header h1 {
  font-size: 3rem;
  margin-bottom: 0.5rem;
}

.highlight {
  color: #2ecc71;
}

.home-header p {
  font-size: 1.3rem;
  color: #555;
}

.features {
  display: flex;
  gap: 2rem;
  justify-content: center;
  flex-wrap: wrap;
  margin-bottom: 3rem;
}

.feature {
  background: #f9f9f9;
  border-radius: 10px;
  padding: 2rem;
  flex: 1 1 250px;
  box-shadow: 0 4px 10px rgba(0,0,0,0.05);
  transition: box-shadow 0.3s ease;
  text-align: center;
}

.feature:hover {
  box-shadow: 0 6px 20px rgba(46, 204, 113, 0.4);
}

.feature h2 {
  margin-bottom: 1rem;
  color: #27ae60;
}

.feature p {
  font-size: 1rem;
  color: #666;
}

.getting-started {
  text-align: center;
  padding: 2rem 1rem;
  border-top: 1px solid #ddd;
}

.getting-started h2 {
  font-size: 2rem;
  margin-bottom: 1rem;
  color: #2ecc71;
}

.getting-started p {
  font-size: 1.1rem;
  color: #444;
  margin-bottom: 2rem;
}

.buttons {
  display: flex;
  justify-content: center;
  gap: 1.5rem;
}

.btn {
  display: inline-block;
  padding: 0.8rem 2rem;
  border-radius: 25px;
  font-weight: 700;
  text-decoration: none;
  cursor: pointer;
  transition: background-color 0.3s ease;
  font-size: 1.1rem;
}

.btn-green {
  background-color: #2ecc71;
  color: white;
  border: none;
}

.btn-green:hover {
  background-color: #27ae60;
}

.btn-outline {
  border: 2px solid #2ecc71;
  color: #2ecc71;
  background: transparent;
}

.btn-outline:hover {
  background-color: #2ecc71;
  color: white;
}
